import { buildUCv2Url } from './ucv2/build-UCv2-url';
import { buildUCv3Url } from './ucv3/build-UCv3-url';
import { CheckoutData } from './CheckoutData';
import { CheckoutType } from './CheckoutType';
import { WorkflowStep } from './WorkflowStep';

export function buildCheckoutUrl(checkoutType: CheckoutType, checkoutData: CheckoutData): string {
  switch (checkoutType) {
    case CheckoutType.V2:
      return buildUCv2Url(checkoutData);
    case CheckoutType.V3:
      return buildUCv3Url(checkoutData);
    default:
      return '';
  }
}

export { WorkflowStep, CheckoutType };
