import { ProviderEnvironment } from '@pandora/data-source-utils';
import { WorkflowStep } from './WorkflowStep';

/**
 * Interface defining data required to build the Checkout URL.
 */
export interface CheckoutData {
  env: ProviderEnvironment;
  workflowStep: WorkflowStep;
  cli: string;
  co: string; // ISO2 country code: 'US', 'FR', etc
  authCode?: string;  // ucv2 only
  items?: Array<Item>;
  lang?: string;
  marketSegment?: string;
  offerType?: string;
  productArrangementCode?: string;
}

export interface Item {
    id: string;
    countrySpecific?: string;
    quantity?: string;
}

const PARAMETERS = new Map<string, string>([
    ["countrySpecific", "cs"],
    ["quantity", "q"],
    ["authCode", "code"],
    ["checkoutPromoCode", "apc"],
    ["rurl", "rUrl"],
    ["curl", "cUrl"],
    ["ctxrturl", "ctxRtUrl"],
]);

export const getParameterName = (field: string) => PARAMETERS.get(field) ?? field;
