import {
    createPlaceholderContext,
    isPlaceholderContext,
} from './placeholderContext';
import { scaffoldModule } from './scaffolds';
import { isFunction, isObject, toArray } from './utilities';

const { createAssert, defineModule, isModule } = scaffoldModule(
    'PlaceholderProvider'
);

const assertIdentifyArgument = createAssert(
    isFunction,
    'Argument "identify" must be a function:'
);
const assertProvideArgument = createAssert(
    isFunction,
    'Argument "provide" must be a function:'
);
const assertIdentifyReturn = createAssert(
    isObject,
    'Method "identify" must return an object:'
);
const assertProvideReturn = createAssert(
    isFunction,
    'Method "provide" must return array of async functions:'
);
const assertProvideResult = createAssert(
    (value) => isObject(value) && isPlaceholderContext(value.context),
    'Method "provide" must return array of async functions each resolving to array of [PlaceholderContext, value] entries:'
);

/**
 * @template Options, Value
 * @type { import('./types').createPlaceholderProvider<Options, Value> }
 */
function createPlaceholderProvider(identify, provide, ...options) {
    if (!assertIdentifyArgument(identify) || !assertProvideArgument(provide)) {
        return null;
    }
    return defineModule({
        identify(context) {
            const identity = identify(
                options.length
                    ? createPlaceholderContext([context, options], context)
                    : context
            );
            if (identity == null || !assertIdentifyReturn(identity)) {
                return null;
            }
            return isPlaceholderContext(identity)
                ? identity
                : createPlaceholderContext(identity);
        },
        provide(contexts) {
            const fetchers = provide(contexts);
            return fetchers
                .filter(assertProvideReturn)
                .map(
                    (fetcher) => () =>
                        Promise.resolve(fetcher()).then((result) =>
                            toArray(result).filter(assertProvideResult)
                        )
                );
        },
    });
}

/**
 * @type { import('./types').isPlaceholderProvider }
 */
const isPlaceholderProvider = isModule;

export { createPlaceholderProvider, isPlaceholderProvider };
