import { scaffoldModule } from './scaffolds';
import {
    isFunction,
    isNumber,
    isPositiveFiniteNumber,
    isPositiveNumber,
} from './utilities';

const { createAssert, defineModule, isModule } = scaffoldModule(
    'PlaceholderResolver'
);

const assertAttemptsSetting = createAssert(
    isPositiveNumber,
    'Setting "attempts" must be a positive number or infinity:'
);
const assertDelaySetting = createAssert(
    isPositiveFiniteNumber,
    'Setting "delay" must be a positive finite number:'
);
const assertTimeoutSetting = createAssert(
    isPositiveNumber,
    'Setting "timeout" must be a positive number or infinity'
);
const assertMethodArgiment = createAssert(
    isFunction,
    'Argument "method" must be a function:'
);
const assertTimeoutArgument = createAssert(
    isNumber,
    'Argument "timeout" must be a positive number or infinity'
);

const placeholderResolverDefaultSettings = {
    attempts: 1,
    delay: 10000,
    timeout: 60000,
};

/** @type { import('./types').createPlaceholderResolver } */
function createPlaceholderResolver(settings = {}) {
    settings = Object.freeze(
        Object.assign({ ...placeholderResolverDefaultSettings }, settings)
    );
    if (
        !assertAttemptsSetting(settings.attempts) ||
        !assertDelaySetting(settings.delay) ||
        !assertTimeoutSetting(settings.timeout)
    ) {
        return null;
    }

    /**
     * @param { Function } method
     * @param { number } timeout
     * @returns { Promise }
     */
    const resolve = (method, timeout = Date.now() + settings.timeout) =>
        new Promise((resolve, reject) => {
            if (
                !assertMethodArgiment(method) ||
                !assertTimeoutArgument(timeout)
            ) {
                reject();
            }
            let attempt = 1;
            function retry() {
                new Promise((resolve) => resolve(method())).then(
                    resolve,
                    (error) => {
                        if (
                            ++attempt > settings.attempts ||
                            Date.now() > timeout
                        ) {
                            reject(error);
                        } else {
                            setTimeout(retry, settings.delay);
                        }
                    }
                );
            }
            retry();
        });

    return defineModule({
        resolve,
        settings,
    });
}

/**
 * @type { import('./types').isPlaceholderResolver }
 */
const isPlaceholderResolver = isModule;

export {
    createPlaceholderResolver,
    isPlaceholderResolver,
    placeholderResolverDefaultSettings,
};
