const namespace = ['dexter', 'tacocat'];

/**
 * @template Property
 * Assigns a property to the global namespace and returns its value.
 * Does not create the preporty if it already exists.
 * @param { string | string[] } keys
 * A key or array of keys to associate created property with.
 * If array, hierarchy of properties will be created.
 * @param { Property | () => Property } value
 * Scalar value to assign to the created property or a function producing the value.
 * @returns { Property }
 * Value of the created or resolved property.
 */
function createGlobal(keys, value) {
    keys = isArray(keys) ? [...namespace, ...keys] : [...namespace, keys];
    const last = keys.length - 1;
    let target = globalThis;
    for (let i = 0; i <= last; i++) {
        const key = keys[i];
        if (!target.hasOwnProperty(key)) {
            Object.defineProperty(target, key, {
                value: i < last ? {} : isFunction(value) ? value() : value,
            });
        }
        target = target[key];
    }
    return target;
}

/**
 * Deletes all properties on `target` object having values which do not pass `test`.
 * If `test` is not provided, deletes all properties having `null` or `undefined` value.
 * @template Target
 * @param { Target } target
 * @param { (value) => boolean } test
 * @returns { Partial<Target> }
 */
function omitProperties(target, test = (value) => value == null) {
    Object.entries(target).forEach(([key, value]) => {
        if (test(value)) delete target[key];
    });
    return target;
}

/**
 * @type { import('./types').isArray }
 */
const isArray = (value) => Array.isArray(value);

/**
 * @type { import('./types').isBoolean }
 */
const isBoolean = (value) => typeof value === 'boolean';

/**
 * @type { import('./types').isFunction }
 */
const isFunction = (value) => typeof value === 'function';

/**
 * @type { import('./types').isNumber }
 */
const isNumber = (value) => typeof value === 'number';

/**
 * @type { import('./types').isObject }
 */
const isObject = (value) => value != null && typeof value === 'object';

/**
 * @type { import('./types').isRegExp }
 */
const isRegExp = (value) => value != null && value instanceof RegExp;

/**
 * @type { import('./types').isString }
 */
const isString = (value) => typeof value === 'string';

/**
 * @type { import('./types').shallowEqual }
 */
function shallowEqual(left, right) {
    if (!(isObject(left) && isObject(right))) {
        return false;
    }
    const keys1 = Object.keys(left);
    const keys2 = Object.keys(right);
    if (keys1.length !== keys2.length) {
        return false;
    }
    for (let key of keys1) {
        if (left[key] !== right[key]) {
            return false;
        }
    }
    return true;
}

/**
 * @type { import('./types').isNotEmptyString }
 */
const isNotEmptyString = (value) => isString(value) && value;

const isPositiveFiniteNumber = (value) =>
    isNumber(value) && Number.isFinite(value) && value > 0;

const isPositiveNumber = (value) => isNumber(value) && value > 0;

const toArray = (value) => (isArray(value) ? value : [value]);

const toBoolean = (value) =>
    isBoolean(value) ? value : ['1', 'true'].includes(String(value));

const escapeHtml = (html) =>
    `${html ?? ''}`.replace(
        /[&<>'"]/g,
        (tag) =>
            ({
                '&': '&amp;',
                '<': '&lt;',
                '>': '&gt;',
                "'": '&#39;',
                '"': '&quot;',
            }[tag] || tag)
    ) ?? '';

export {
    createGlobal,
    isArray,
    isBoolean,
    isFunction,
    isNotEmptyString,
    isNumber,
    isObject,
    isPositiveFiniteNumber,
    isPositiveNumber,
    isRegExp,
    isString,
    toArray,
    toBoolean,
    escapeHtml,
    omitProperties,
    shallowEqual,
};
