import {CheckoutData, Item, getParameterName} from './CheckoutData';
import {ProviderEnvironment} from '@pandora/data-source-utils';
import {WorkflowStep} from "./WorkflowStep";

export function build(checkoutData: CheckoutData, workflowStepFormatter: Function): string {
  const { env, items, workflowStep, marketSegment, offerType, productArrangementCode, ...rest } = checkoutData;
  const hostname = getHostName(env);
  const parameters = new URLSearchParams();
  const formattedWorkflowStep = workflowStepFormatter(workflowStep);
  if (workflowStep !== WorkflowStep.SEGMENTATION) {
    setItemsParameter(items, parameters);
  }
  for (const [key, value] of Object.entries(rest)) {
    if (value != null) parameters.set(getParameterName(key), value);
  }

  if (workflowStep === WorkflowStep.SEGMENTATION) {
      // todo: ms & pa are required parameters, if any is null we should return empty string
      if (marketSegment != null) parameters.set('ms', marketSegment);
      if (offerType != null) parameters.set('ot', offerType);
      if (productArrangementCode != null) parameters.set('pa', productArrangementCode);
  }
  const parametersString = getParametersString(parameters);
  return `${hostname}${formattedWorkflowStep}${parametersString}`;
}

function getHostName(env: ProviderEnvironment): string {
  switch (env) {
    case ProviderEnvironment.PRODUCTION:
      return 'https://commerce.adobe.com';
    case ProviderEnvironment.STAGE:
      return 'https://commerce-stg.adobe.com';
    default:
      return 'https://commerce-stg.adobe.com';
  }
}

function setItemsParameter(items: Array<Item>, parameters: URLSearchParams): void {
  for (const idx in items) {
    const item = items[idx];
    for (const [key, value] of Object.entries(item)) {
      if (value == null) continue;
      const parameterName = getParameterName(key); // countrySpecific -> cs
      parameters.set(`items[${idx}][${parameterName}]`, value.toString());
    }
  }
}

function getParametersString(parameters: URLSearchParams) {
  if (!parameters) {
    return '#';
  }
  return `?${parameters.toString()}`;
}
