import {
    Button,
    Divider,
    Flex,
    Grid,
    Heading,
    Image,
    Item,
    ListBox,
    Picker,
    Radio,
    RadioGroup,
    Text,
    TextField,
    View,
} from '@adobe/react-spectrum';
import React, {
    useCallback,
    useContext,
    useEffect,
    useMemo,
    useState,
} from 'react';
import PropTypes from 'prop-types';

import {
    OfferType,
    CustomerSegment,
    MarketSegment,
    Commitment,
    Term,
} from '@pandora/data-models-odm';
import Magnify from '@spectrum-icons/workflow/Magnify';

import AppContext from './AppContext';
import { PrevNext } from './PropTypes';

function ProductsTab({ setAosParams, setSelectedProduct, nextStep }) {
    const { products, aosParams, searchArrangementCode, onCancel } =
        useContext(AppContext);
    if (!products) return <></>;
    const [searchText, setSearchText] = useState();
    const [[commitment, term], setPlanType] = useState([
        aosParams.commitment,
        aosParams.term,
    ]);
    const [customerSegment, setCustomerSegment] = useState(
        aosParams.customerSegment
    );
    const [offerType, setOfferType] = useState(aosParams.offerType);
    const [marketSegment, setMarketSegment] = useState(aosParams.marketSegment);

    // fulltext search that
    const productListItems = useMemo(() => {
        const criteria = new RegExp(searchText, 'i');
        return Object.entries(products)
            .filter(
                ([, { arrangement_code, name }]) =>
                    criteria.test(name) || criteria.test(arrangement_code)
            )
            .filter(([, { customerSegments, marketSegments }]) => {
                return (
                    customerSegments[customerSegment] === true &&
                    marketSegments[marketSegment] === true
                );
            })
            .map(([code, product]) => ({ id: code, code, ...product }));
    }, [searchText, customerSegment, marketSegment]);

    // tab 1 / products
    const [selectedProductKeys, setSelectedProductKeys] = useState([]);
    const selectedProduct = useMemo(() => {
        if (selectedProductKeys.length === 0) return undefined;
        const [singleSelection] = selectedProductKeys;
        return productListItems.find(({ code }) => {
            return code === singleSelection;
        });
    }, [productListItems, selectedProductKeys]);

    useEffect(() => {
        if (searchArrangementCode) {
            if (!selectedProduct) {
                setSelectedProductKeys([searchArrangementCode]);
            } else {
                nextStep();
            }
        }
    }, [searchArrangementCode, selectedProduct]);

    useEffect(() => {
        // find selected product via its key.
        setSelectedProduct(selectedProduct);
    }, [selectedProduct]);

    useEffect(() => {
        // persist selected parameters to the state.
        setAosParams({
            arrangementCode: selectedProduct?.code,
            commitment,
            term,
            customerSegment,
            offerType,
            marketSegment,
        });
    }, [
        commitment,
        term,
        selectedProduct,
        customerSegment,
        offerType,
        marketSegment,
    ]);

    const productList = (
        <View
            backgroundColor="gray-50"
            gridArea="products"
            borderRadius="regular"
            borderWidth="thin"
            borderColor="light"
            paddingTop="static-size-200"
            paddingStart="static-size-200"
            paddingEnd="static-size-200"
        >
            <Flex
                direction="column"
                alignItems="center"
                width="100%"
                maxHeight="450px"
            >
                <TextField
                    placeholder="Search by name or product code"
                    icon={<Magnify />}
                    onChange={setSearchText}
                    data-testid="search"
                    width="static-size-6000"
                    autoFocus
                    defaultValue={searchText}
                />
                <Divider
                    size="S"
                    marginTop="static-size-200"
                    marginBottom="static-size-200"
                />
                <ListBox
                    aria-label="Products"
                    alignSelf="stretch"
                    justifySelf="stretch"
                    items={productListItems}
                    selectionMode="single"
                    selectedKeys={selectedProductKeys}
                    onSelectionChange={(selected) =>
                        setSelectedProductKeys(Array.from(selected))
                    }
                >
                    {({ code, name, icon }) => (
                        <Item textValue={code}>
                            <Image
                                gridArea="icon"
                                maxWidth="48px"
                                height="48px"
                                src={icon}
                                alt="Product Icon"
                            />
                            <Text
                                gridArea="text"
                                data-testid={code}
                                alignSelf="center"
                                UNSAFE_className="productName"
                            >
                                {name}
                            </Text>
                            <Text
                                gridArea="description"
                                alignSelf="center"
                                UNSAFE_className="productName"
                            >
                                {code}
                            </Text>
                        </Item>
                    )}
                </ListBox>
            </Flex>
        </View>
    );
    const defaultPlanType = useMemo(() => {
        const planType = [];
        if (commitment) planType.push(commitment);
        if (term) planType.push(term);
        return planType.join('-');
    }, []);

    // tab 1 / filters
    const filters = (
        <View
            backgroundColor="gray-50"
            gridArea="entitlements"
            borderRadius="regular"
            borderWidth="thin"
            borderColor="light"
            paddingStart="static-size-200"
            paddingEnd="static-size-200"
        >
            <Flex direction="column" width="100%" height="100%">
                <Heading level="3">Select your entitlements</Heading>
                <Divider
                    size="S"
                    marginTop="static-size-100"
                    marginBottom="static-size-200"
                />
                <Flex
                    direction="column"
                    width="100%"
                    height="100%"
                    justifyContent="space-evenly"
                >
                    <Picker
                        label="Choose your plan type*"
                        defaultSelectedKey={defaultPlanType}
                        width="static-size-4600"
                        onSelectionChange={(value) =>
                            setPlanType(value.split('-'))
                        }
                    >
                        <Item key={''}>ALL (ABM, PUF, M2M, PERP)</Item>
                        <Item key={`${Commitment.YEAR}-${Term.MONTHLY}`}>
                            ABM
                        </Item>
                        <Item key={`${Commitment.YEAR}-${Term.ANNUAL}`}>
                            PUF
                        </Item>
                        <Item key={`${Commitment.MONTH}-${Term.MONTHLY}`}>
                            M2M
                        </Item>
                        <Item key={`${Commitment.PERPETUAL}`}>PERP</Item>
                    </Picker>
                    <RadioGroup
                        label="Choose your customer segment*"
                        orientation="horizontal"
                        defaultValue={customerSegment}
                        onChange={setCustomerSegment}
                    >
                        <Radio value="INDIVIDUAL">
                            {CustomerSegment.INDIVIDUAL}
                        </Radio>
                        <Radio value="TEAM">{CustomerSegment.TEAM}</Radio>
                    </RadioGroup>
                    <Picker
                        label="Choose your offer type*"
                        defaultSelectedKey={offerType}
                        width="static-size-4600"
                        onSelectionChange={setOfferType}
                    >
                        <Item key={OfferType.BASE}>{OfferType.BASE}</Item>
                        <Item key={OfferType.TRIAL}>{OfferType.TRIAL}</Item>
                        <Item key={OfferType.PROMOTION}>
                            {OfferType.PROMOTION}
                        </Item>
                    </Picker>
                    <RadioGroup
                        label="Choose your market segment*"
                        orientation="horizontal"
                        defaultValue={marketSegment}
                        onChange={setMarketSegment}
                    >
                        <Radio value="COM">{MarketSegment.COM}</Radio>
                        <Radio value="EDU">{MarketSegment.EDU}</Radio>
                        <Radio value="GOV">{MarketSegment.GOV}</Radio>
                    </RadioGroup>
                </Flex>
            </Flex>
        </View>
    );

    return (
        <View paddingTop="static-size-300">
            <Grid
                areas={[
                    'header  header',
                    'products entitlements',
                    'footer  footer',
                ]}
                columns={['1fr', '1fr']}
                rows={['size-500', 'auto', 'size-800']}
                height="650px"
                gap="static-size-400"
            >
                <Heading level="2" gridArea="header">
                    Select your product from the list below
                </Heading>
                {productList}
                {filters}
                <View justifySelf="end" gridArea="footer">
                    {onCancel && (
                        <Button
                            marginEnd="static-size-200"
                            aria-label="cancel"
                            variant="primary"
                            onPress={onCancel}
                        >
                            Cancel
                        </Button>
                    )}
                    <Button
                        aria-label="next-step"
                        variant="cta"
                        isDisabled={!selectedProduct}
                        onPress={() => nextStep(true)}
                    >
                        Next
                    </Button>
                </View>
            </Grid>
        </View>
    );
}

ProductsTab.propTypes = {
    setAosParams: PropTypes.func.isRequired,
    setSelectedProduct: PropTypes.func.isRequired,
    ...PrevNext,
};

export default ProductsTab;
