import {
    Button,
    Checkbox,
    CheckboxGroup,
    Flex,
    Grid,
    Heading,
    Text,
    ToggleButton,
    View,
} from '@adobe/react-spectrum';
import React, {
    useCallback,
    useContext,
    useEffect,
    useLayoutEffect,
    useMemo,
    useState,
} from 'react';
import PropTypes from 'prop-types';

import ChevronDoubleLeft from '@spectrum-icons/workflow/ChevronDoubleLeft';
import ChevronDoubleRight from '@spectrum-icons/workflow/ChevronDoubleRight';

import { useOfferSearch, useOfferSelector } from '@pandora/data-source-offers';
import { AosClient, ServiceProvider } from '@pandora/data-source-aos';
import { BuyingProgram, SalesChannel } from '@pandora/data-models-odm';

import { applyPlanType } from '@dexter/tacocat-wcs-client';

import { PlaceholderContext } from '@dexter/tacocat-react-placeholders';

import resolveOfferSelector from './resolveOfferSelector';

import Offer from './Offer';
import AppContext from './AppContext';
import { PrevNext } from './PropTypes';

import PlaceholderKey from './PlaceholderKey';
import { shallowEqual } from '@dexter/tacocat-core';

const defaultAosParams = {
    buyingProgram: BuyingProgram.RETAIL,
    merchant: 'ADOBE',
    salesChannel: SalesChannel.DIRECT,
    serviceProviders: [ServiceProvider.PRICING],
};

function OffersTab({ aosParams, selectedProduct, previousStep, onSelect }) {
    const {
        accessToken,
        apiKey,
        onCancel,
        defaultPlaceholderOptions,
        offerSelectorPlaceholderOptions,
        showPlaceholderOptions,
        showPlaceholders,
        disableOfferSelection,
        globals,
        country,
        selectOfferId,
        resetParams,
        baseUrl,
        env,
        environment,
        landscape,
        engine,
        placeholderTypes,
    } = useContext(AppContext);

    const queryOptions = {
        apiKey,
        environment,
        landscape,
        pageSize: 1000,
    };

    const [offers, setOffers] = useState([]);

    const isDefaultOptions = useMemo(() => {
        if (Object.keys(offerSelectorPlaceholderOptions).length === 0)
            return true;
        return shallowEqual(
            offerSelectorPlaceholderOptions,
            defaultPlaceholderOptions
        );
    }, []);

    const [expandPlaceholderOptions, setExpandPlaceholderOptions] = useState(
        !isDefaultOptions
    );

    const [placeholderOptions, setPlaceholderOptions] = useState([]);

    // initialize the placeholder option checkboxes
    useEffect(() => {
        setPlaceholderOptions(
            Object.entries({
                ...defaultPlaceholderOptions,
                ...offerSelectorPlaceholderOptions,
            })
                .filter(([, value]) => !value)
                .map(([key]) => key)
        );
    }, []);

    const [selectedOfferSelectorReady, setSelectedOfferSelectorReady] =
        useState(false);
    const [offerSelectorId, setOfferSelectorId] = useState();
    const [selectedOffer, setSelectedOffer] = useState();

    // this is invoked when the use button is clicked on a placeholder
    const onSelectWithAosParams = (osi, type, placeholderOptions) => {
        if (!selectedOffer) return;
        if (showPlaceholderOptions) {
            onSelect(osi, type, selectedOffer, placeholderOptions);
        } else {
            onSelect(osi, type, selectedOffer);
        }
    };

    const {
        arrangementCode,
        commitment,
        term,
        offerType,
        customerSegment,
        marketSegment,
    } = aosParams;
    const { planType } = applyPlanType({ commitment, term });
    // tab 2 / result text
    const resultText = useMemo(() => {
        if (!aosParams) return null;
        return ` ${selectedProduct.name}, ${
            planType ? planType + ',' : ''
        } ${customerSegment}, ${offerType}, ${marketSegment}`;
    }, [aosParams, planType, offerType, customerSegment, marketSegment]);

    const search = useOfferSearch((fetchOptions) => {
        const finalFetchOptions = {
            ...fetchOptions,
            accessToken,
            apiKey,
            env,
            baseUrl,
        };
        return new AosClient(finalFetchOptions);
    });

    useEffect(() => {
        search(
            {
                ...defaultAosParams,
                country,
                offerType,
                customerSegment,
                marketSegment,
                arrangementCode: [arrangementCode],
                commitment,
                language: country === 'GB' ? 'EN' : 'MULT',
                term,
            },
            queryOptions
        )
            .then((res) => res.data)
            .then((offers) => offers.map(applyPlanType))
            .then((offers) =>
                offers.map((offer) => {
                    offer.id = offer.offer_id;
                    offer.name = selectedProduct.name;
                    offer.icon = selectedProduct.icon;
                    return offer;
                })
            )
            .then((offers) =>
                offers.sort(
                    (
                        { name: nameLeft, price_point: pricePointLeft },
                        { name: nameRight, price_point: pricePointRight }
                    ) => {
                        return `${nameRight}${pricePointRight}`.localeCompare(
                            `${nameLeft}${pricePointLeft}`
                        );
                    }
                )
            )
            .then(setOffers);
    }, [resultText]);

    const [createOfferSelector] = useOfferSelector((fetchOptions) => {
        const finalFetchOptions = {
            ...fetchOptions,
            accessToken,
            apiKey,
            env,
            baseUrl,
        };
        return new AosClient(finalFetchOptions);
    });

    // create offer selector for current search
    useLayoutEffect(() => {
        if (offers.length === 0) return;
        const [{ buying_program, merchant, sales_channel }] = offers;
        const {
            arrangementCode: product_arrangement_code,
            commitment,
            customerSegment: customer_segment,
            marketSegment: market_segment,
            offerType: offer_type,
            term,
        } = aosParams;

        const offerSelectorParams = {
            buying_program,
            merchant,
            sales_channel,
            product_arrangement_code,
            customer_segment,
            market_segments: [market_segment],
            offer_type,
        };
        if (commitment && term) {
            Object.assign(offerSelectorParams, {
                commitment,
                term,
            });
        }
        if (disableOfferSelection) {
            resolveOfferSelector(
                offerSelectorParams,
                createOfferSelector,
                queryOptions
            ).then(setOfferSelectorId);
        }
    }, [offers, disableOfferSelection, createOfferSelector]);

    // create offer selector for current offer
    useLayoutEffect(() => {
        if (!selectedOffer) return;

        resolveOfferSelector(
            selectedOffer,
            createOfferSelector,
            queryOptions
        ).then((value) => {
            setOfferSelectorId(value);
            setSelectedOfferSelectorReady(true);
        });
    }, [selectedOffer, createOfferSelector]);

    useLayoutEffect(() => {
        if (!selectOfferId) return;
        const offer = offers.find(({ offer_id }) => offer_id === selectOfferId);
        if (offer) {
            resetParams();
            setSelectedOffer(offer);
        }
    }, [offers]);

    const placeholderKeys = useMemo(() => {
        if (!(selectedOfferSelectorReady && offerSelectorId && engine))
            return <></>;

        const placeholders = placeholderTypes.map(
            ({ type, name, description, doc }) => {
                const key = `${offerSelectorId}.${type}`;
                return (
                    <PlaceholderKey
                        key={key}
                        offerSelectorId={offerSelectorId}
                        type={type}
                        name={name}
                        description={description}
                        doc={doc}
                        onSelect={onSelectWithAosParams}
                        options={placeholderOptions}
                    />
                );
            }
        );

        return (
            <PlaceholderContext.Provider value={{ engine, globals }}>
                <Grid
                    columns={['1fr', 'static-size-1000']}
                    autoRows={[
                        'static-size-400',
                        'auto',
                        'static-size-400',
                        'static-size-400',
                    ]}
                >
                    {placeholders}
                </Grid>
            </PlaceholderContext.Provider>
        );
    }, [
        selectedOfferSelectorReady,
        offerSelectorId,
        engine,
        globals,
        placeholderOptions,
        onSelectWithAosParams,
    ]);

    const offerSelectHandler = useCallback((offer) => {
        if (disableOfferSelection) return;
        setSelectedOffer(offer);
    }, []);

    const useClickHandler = useCallback(() => {
        onSelect(offerSelectorId, null, selectedOffer);
    }, [selectedOffer, offerSelectorId]);

    const offerListView = useMemo(() => {
        if (offers.length === 0) return <View gridColumn="span 2" />;
        const offersView = offers.map((offer) => (
            <div key={offer.offer_id} onClick={() => offerSelectHandler(offer)}>
                <Offer
                    selected={offer === selectedOffer}
                    offer={{
                        ...offer,
                        ...selectedProduct,
                    }}
                />
            </div>
        ));
        return (
            <Flex
                direction="column"
                gap="static-size-40"
                aria-label="Offers"
                alignSelf="stretch"
                justifySelf="stretch"
            >
                {offersView}
            </Flex>
        );
    }, [offers, selectedOffer]);
    return (
        <View paddingTop="static-size-450" height="650px">
            <Grid
                columns={['1fr', '1fr', '1fr', '1fr', '1fr']}
                rows={[
                    'static-size-500',
                    'static-size-500',
                    'static-size-1200',
                    '1fr',
                ]}
                columnGap="size-300"
                height="550px"
            >
                <Heading level="2" gridColumn="span 5" margin="0">
                    {disableOfferSelection
                        ? 'Use the offers as an offer selector'
                        : 'Select your offer from the list below'}
                </Heading>
                <Text gridColumn="span 5">
                    Showing results for {resultText}
                </Text>
                <View
                    gridColumn={showPlaceholders ? '1 / span 3' : '1 / span 5'}
                    gridRow="3 / span 3"
                    overflow="auto"
                    borderRadius="regular"
                    borderWidth="thin"
                    borderColor="light"
                    backgroundColor="gray-50"
                    padding="static-size-200"
                >
                    {offerListView}
                </View>
                {showPlaceholders && showPlaceholderOptions && (
                    <View
                        backgroundColor="gray-300"
                        height="static-size-1000"
                        gridColumn="4 / span 2"
                        paddingStart="static-size-200"
                        paddingEnd="static-size-200"
                        borderRadius="regular"
                        borderWidth="thin"
                        borderColor="light"
                    >
                        {selectedOffer && (
                            <Flex
                                direction="row-reverse"
                                justifyContent="space-between"
                                alignItems="center"
                                height="100%"
                            >
                                <ToggleButton
                                    isEmphasized
                                    isSelected={expandPlaceholderOptions}
                                    onChange={setExpandPlaceholderOptions}
                                    aria-label="Options"
                                >
                                    {expandPlaceholderOptions ? (
                                        <ChevronDoubleRight />
                                    ) : (
                                        <ChevronDoubleLeft />
                                    )}
                                </ToggleButton>
                                {expandPlaceholderOptions && (
                                    <CheckboxGroup
                                        label="Disable"
                                        orientation="horizontal"
                                        value={placeholderOptions}
                                        onChange={setPlaceholderOptions}
                                    >
                                        <Checkbox value="displayFormatted">
                                            Formatting
                                        </Checkbox>
                                        <Checkbox value="displayRecurrence">
                                            Term
                                        </Checkbox>
                                        <Checkbox value="displaySeat">
                                            Seat
                                        </Checkbox>
                                        <Checkbox value="displayTax">
                                            Tax
                                        </Checkbox>
                                    </CheckboxGroup>
                                )}
                            </Flex>
                        )}
                    </View>
                )}
                {showPlaceholders && (
                    <View
                        backgroundColor="gray-200"
                        overflow="scroll"
                        gridColumn="4 / span 2"
                        gridRow={
                            showPlaceholderOptions ? '4 / span 1' : '3 / span 2'
                        }
                        paddingStart="static-size-200"
                        paddingEnd="static-size-200"
                        borderRadius="regular"
                        borderWidth="thin"
                        borderColor="light"
                    >
                        {placeholderKeys}
                    </View>
                )}
            </Grid>
            <Flex
                justifyContent="space-between"
                marginBottom="static-size-400"
                marginTop="static-size-400"
            >
                <Button
                    aria-label="previous-step"
                    variant="primary"
                    onPress={previousStep}
                >
                    Back
                </Button>
                <Flex>
                    {onCancel && (
                        <Button
                            aria-label="cancel"
                            variant="primary"
                            onPress={onCancel}
                        >
                            Cancel
                        </Button>
                    )}
                    {!showPlaceholders && (
                        <Button
                            marginStart="static-size-200"
                            aria-label="previous-step"
                            variant="cta"
                            onPress={useClickHandler}
                            isDisabled={!offerSelectorId}
                        >
                            Use
                        </Button>
                    )}
                </Flex>
            </Flex>
        </View>
    );
}

OffersTab.propTypes = {
    aosParams: PropTypes.object.isRequired,
    onSelect: PropTypes.func.isRequired,
    selectedProduct: PropTypes.shape({
        arrangement_code: PropTypes.string,
    }),
    ...PrevNext,
};
OffersTab.defaultProps = { aosParams: {} };

export default OffersTab;
