import { buildCheckoutUrl } from '@dexter/commerce-checkout-url-builder';
import {
    price,
    priceOptical,
    priceStrikethrough,
} from '@dexter/tacocat-consonant-templates';
import { placeholderStages } from '@dexter/tacocat-core';
import {
    createDomAttributePlaceholderTemplate,
    createDomHtmlPlaceholderTemplate,
} from '@dexter/tacocat-dom-placeholders';

const { resolved } = placeholderStages;

const wcsCheckoutUrlDomPlaceholderTemplate =
    createDomAttributePlaceholderTemplate(
        (context) => context.type === 'checkoutUrl',
        (
            {
                checkoutWorkflow,
                checkoutWorkflowStep,
                checkoutClientId: cli,
                checkoutCtx: ctx,
                country: co,
                env,
                language: lang,
                checkoutMarketSegment,
                checkoutCountrySpecific,
                checkoutQuantity,
                authCode,
                checkoutPromoCode,
                customParameters,
            },
            { offerId, offerType, productArrangementCode }
        ) =>
            // TODO: make checkoutWorkflow case-insensitive for simplicity of use
            buildCheckoutUrl(checkoutWorkflow, {
                cli,
                co,
                authCode,
                ctx,
                env,
                items: [
                    {
                        id: offerId,
                        countrySpecific: checkoutCountrySpecific,
                        quantity: checkoutQuantity,
                    },
                ],
                lang,
                workflowStep: checkoutWorkflowStep,
                marketSegment: checkoutMarketSegment,
                offerType,
                productArrangementCode,
                checkoutPromoCode,
                ...customParameters,
                // NOTE: any extra parameter passed here will be appended to the unified checkout URL
            }),
        resolved,
        'href'
    );

const [
    wcsPriceDomPlaceholderTemplate,
    wcsPriceOpticalDomPlaceholderTemplate,
    wcsPriceStrikethroughDomPlaceholderTemplate,
] = Object.entries({
    price,
    priceOptical,
    priceStrikethrough,
}).map(([type, template]) =>
    createDomHtmlPlaceholderTemplate(
        (context) => context.type === type,
        (
            context,
            {
                analytics,
                commitment,
                priceDetails: {
                    formatString,
                    price,
                    taxDisplay,
                    taxTerm,
                    usePrecision,
                },
                term,
            }
        ) =>
            template(
                context,
                {
                    commitment,
                    formatString,
                    price,
                    taxDisplay,
                    taxTerm,
                    usePrecision,
                    term,
                },
                { 'data-analytics-productinfo': analytics }
            ),
        resolved
    )
);

export {
    wcsCheckoutUrlDomPlaceholderTemplate,
    wcsPriceDomPlaceholderTemplate,
    wcsPriceOpticalDomPlaceholderTemplate,
    wcsPriceStrikethroughDomPlaceholderTemplate,
};
