import {
    escapeHtml,
    isObject,
    isNumber,
    isString,
    toBoolean,
} from '@dexter/tacocat-core';
import IntlMessageFormat from 'intl-messageformat';
import { formatOpticalPrice, formatRegularPrice } from './utilities';

const cssClassNames = {
    currencySpace: 'price-currency-space',
    currencySymbol: 'price-currency-symbol',
    decimals: 'price-decimals',
    decimalsDelimiter: 'price-decimals-delimiter',
    fullDisplay: 'price-full-display',
    integer: 'price-integer',
    recurrence: 'price-recurrence',
    strikethroughDisplay: 'price-strikethrough-display',
    taxInclusivity: 'price-tax-inclusivity',
    taxInclusivitySpace: 'price-tax-inclusivity-space',
};
const literalKeys = {
    recurrenceLabel: 'recurrenceLabel',
    recurrenceAriaLabel: 'recurrenceAriaLabel',
    taxExclusiveLabel: 'taxExclusiveLabel',
    taxInclusiveLabel: 'taxInclusiveLabel',
    strikethroughAriaLabel: 'strikethroughAriaLabel',
};
const taxDisplayExclusive = 'TAX_EXCLUSIVE';

const renderAttributes = (attributes) =>
    isObject(attributes)
        ? Object.entries(attributes)
              .filter(
                  ([, value]) =>
                      isString(value) || isNumber(value) || value === true
              )
              .reduce(
                  (html, [key, value]) =>
                      html +
                      ` ${key}${
                          value === true ? '' : '="' + escapeHtml(value) + '"'
                      }`,
                  ''
              )
        : '';

const renderSpan = (className, content, attributes) =>
    `<span class="${className}"${renderAttributes(
        attributes
    )}>${content}</span>`;

function renderContainer(
    containerCssClassName,
    {
        accessibleLabel,
        currencySymbol,
        decimals,
        decimalsDelimiter,
        hasCurrencySpace,
        integer,
        isCurrencyFirst,
        recurrenceLabel,
        taxInclusivityLabel,
    },
    attributes = {}
) {
    const currencyMarkup = currencySymbol
        ? renderSpan(cssClassNames.currencySymbol, currencySymbol)
        : '';
    let currencySpaceMarkup = hasCurrencySpace
        ? renderSpan(cssClassNames.currencySpace, '&nbsp;')
        : '';

    let markup = '';
    if (isCurrencyFirst && currencySymbol) {
        markup += currencyMarkup + currencySpaceMarkup;
    }
    if (integer) {
        markup += renderSpan(cssClassNames.integer, integer);
    }
    if (decimalsDelimiter) {
        markup += renderSpan(
            cssClassNames.decimalsDelimiter,
            decimalsDelimiter
        );
    }
    if (decimals) {
        markup += renderSpan(cssClassNames.decimals, decimals);
    }
    if (!isCurrencyFirst && currencySymbol) {
        markup += currencySpaceMarkup + currencyMarkup;
    }
    if (recurrenceLabel) {
        markup += renderSpan(cssClassNames.recurrence, recurrenceLabel);
    }
    if (taxInclusivityLabel) {
        markup += renderSpan(cssClassNames.taxInclusivitySpace, '&nbsp;');
        markup += renderSpan(cssClassNames.taxInclusivity, taxInclusivityLabel);
    }

    return renderSpan(containerCssClassName, markup, {
        ...attributes,
        ['aria-label']: accessibleLabel,
    });
}

/**
 * @type { import('./types').createPriceTemplate }
 * Returns a function formatting product prices using provided options.
 */
// TODO: check WCS data elements to include: analytics, endDate, language, merchant, offerType, pricePoint, startDate
const createPriceTemplate =
    ({ displayOptical = false, displayStrikethrough = false } = {}) =>
    (
        {
            country = 'US',
            displayFormatted = true,
            displayRecurrence = true,
            displayTax = false,
            language = 'en',
            literals,
        } = {},
        {
            commitment,
            formatString,
            price,
            taxDisplay,
            taxTerm,
            term,
            usePrecision,
        } = {},
        attributes = {}
    ) => {
        if (formatString == null || literals == null || price == null) {
            return '';
        }

        const locale = `${language.toLowerCase()}-${country.toUpperCase()}`;
        function formatLiteral(key, parameters) {
            const literal = literals[key];
            return literal == null
                ? ''
                : new IntlMessageFormat(literal, locale)
                      .format(parameters)
                      ?.trim();
        }

        if (!toBoolean(displayFormatted)) {
            formatString = '#0.00';
            usePrecision = true;
        }
        const { accessiblePrice, recurrenceTerm, ...formattedPrice } = (
            displayOptical ? formatOpticalPrice : formatRegularPrice
        )({
            commitment,
            formatString,
            price,
            term,
            usePrecision,
        });

        let accessibleLabel = accessiblePrice;
        let recurrenceLabel = '';
        if (toBoolean(displayRecurrence) && recurrenceTerm) {
            const recurrenceAccessibleLabel = formatLiteral(
                literalKeys.recurrenceAriaLabel,
                {
                    recurrenceTerm,
                }
            );
            if (recurrenceAccessibleLabel) {
                accessibleLabel += ' ' + recurrenceAccessibleLabel;
            }
            recurrenceLabel = formatLiteral(literalKeys.recurrenceLabel, {
                recurrenceTerm,
            });
        }

        let taxInclusivityLabel = '';
        if (toBoolean(displayTax) && taxTerm) {
            taxInclusivityLabel = formatLiteral(
                taxDisplay === taxDisplayExclusive
                    ? literalKeys.taxExclusiveLabel
                    : literalKeys.taxInclusiveLabel,
                { taxTerm }
            );
            if (taxInclusivityLabel) {
                accessibleLabel += ' ' + taxInclusivityLabel;
            }
        }

        if (displayStrikethrough) {
            accessibleLabel = formatLiteral(
                literalKeys.strikethroughAriaLabel,
                {
                    strikethroughPrice: accessibleLabel,
                }
            );
        }

        return renderContainer(
            displayStrikethrough
                ? cssClassNames.strikethroughDisplay
                : cssClassNames.fullDisplay,
            {
                ...formattedPrice,
                accessibleLabel,
                recurrenceLabel,
                taxInclusivityLabel,
            },
            attributes
        );
    };

export { createPriceTemplate };
