import { build } from '../build';
import { CheckoutData } from '../CheckoutData';

/**
 * Props for updating or submitting an order
 */

export function buildUCv3Url(checkoutData: UCv3CheckoutData): string {
  if (!validateCheckoutData(checkoutData)) {
    throw new Error('Argument "checkoutData" is not valid');
  }
  delete checkoutData.authCode;
  return build(checkoutData, formatWorkflowStep);
}

function formatWorkflowStep(workflowStep: string): string {
  return `/store/${workflowStep}`;
}

/**
 * Interface defining data required to build the Checkout URL.
 */
interface UCv3CheckoutData extends CheckoutData {
  landscape?: string;
  ctx?: string;
  ctxRtUrl?: string;
  jit?: string;
  productArrangementCode?: string;
  offerType?: string;
  marketSegment?: string;
  apc?: string;
}

function validateCheckoutData({ env, workflowStep, cli, co, items }: UCv3CheckoutData): boolean {
  if (!env || !workflowStep || !cli || !co) {
    return false;
  }
  if (workflowStep !== 'segmentation' && !items) {
    return false;
  }
  return true;
}
