import { build } from '../build';
import { CheckoutData } from '../CheckoutData';

/**
 * Props for updating or submitting an order
 */

export function buildUCv2Url(checkoutData: UCv2CheckoutData): string {
  if (!isCheckoutDataValid(checkoutData)) {
    throw new Error('Argument "checkoutData" is not valid');
  }
  return build(checkoutData, formatWorkflowStep);
}

function formatWorkflowStep(workflowStep: string): string {
  return `/${workflowStep}/`;
}

/**
 * Interface defining data required to build the Checkout URL.
 */
interface UCv2CheckoutData extends CheckoutData {
  authCode?: string;
  ctx?: string;
  cUrl?: string;
  lang?: string; // ISO2 language code: 'en', 'fr', etc
  mv?: string;
  nglwfdata?: string;
  otac?: string;
  promoid?: string;
  rUrl?: string;
  sdid?: string;
  spint?: string;
  trackingid?: string;
}

function isCheckoutDataValid({ env, workflowStep, cli, co, items }: CheckoutData): boolean {
  if (!env || !workflowStep || !cli || !co || !items) {
    return false;
  }
  return true;
}
