import { isArray, isObject, isString } from './utilities';

const epoch = Date.now();

/**
 * Returns `log` object which provides methods for unified error/debug logging
 * from a `source` module specified by its name.
 * Debugging logs are disabled by default and can be enabled by setting `process.env.DEBUG` to true.
 * @type { import("./types").createLog }
 */
function createLog(source) {
    const debugEnabled =
        process.env.DEBUG === 'true' ||
        globalThis.sessionStorage?.getItem('tacocat.debug') === 'true';

    const prefix = `[tacocat/${source}]`;
    const suffix = () => `(+${Date.now() - epoch}ms)`;
    const assert = (condition, message, ...args) => {
        if (!condition) {
            error(message, ...args);
            return false;
        }
        return true;
    };
    const debug = (message, ...args) => {
        if (debugEnabled) {
            console.debug(`${prefix} ${message}`, ...args, suffix());
        }
    };
    const error = (message, ...args) => {
        console.error(`${prefix} ${message}`, ...args, suffix());
        // TODO: integrate with LANA (MWPW-111679)
    };
    const warn = (message, ...args) => {
        console.warn(`${prefix} ${message}`, ...args, suffix());
    };
    return { assert, debug, error, warn };
}

export { createLog };
