import {
    createPlaceholderTemplate,
    isNotEmptyString,
    placeholderStages,
    scaffoldModule,
} from '@dexter/tacocat-core';
import { isElement, isNode } from './domUtilities';

const { pending, rejected, resolved } = placeholderStages;

const cssClassNames = Object.fromEntries(
    [pending, rejected, resolved].map((stage) => [
        stage,
        `placeholder-${stage}`,
    ])
);
const allCssClassNames = Object.values(cssClassNames);

const { log, createAssert } = scaffoldModule('DomPlaceholderTemplate');

const assertAttributeNameArgument = createAssert(
    isNotEmptyString,
    'Argument "attributeName" must be not empty string:'
);

const assertElementPlaceholder = createAssert(
    isElement,
    'Can be only applied to DOM Element placeholder:'
);

const assertNodePlaceholder = createAssert(
    isNode,
    'Can be only applied to DOM Node placeholder:'
);

/**
 * @template Options, Value
 * @type { import('./types').createDomAttributePlaceholderTemplate<Options, Value> }
 * */
const createDomAttributePlaceholderTemplate = (
    accept,
    template,
    stage,
    attributeName
) =>
    assertAttributeNameArgument(attributeName)
        ? createPlaceholderTemplate(
              accept,
              (context, value) => (node) => {
                  if (assertElementPlaceholder(node)) {
                      node.classList.remove(...allCssClassNames);
                      node.classList.add(cssClassNames[stage]);
                      node.setAttribute(
                          attributeName,
                          template(context, value)
                      );
                  }
              },
              stage
          )
        : null;

/**
 * @template Options, Value
 * @type { import('./types').createDomAttributePlaceholderTemplate<Options, Value> }
 * */
const createDomHtmlPlaceholderTemplate = (accept, template, stage) =>
    createPlaceholderTemplate(
        accept,
        (context, value) => (node) => {
            if (assertNodePlaceholder(node)) {
                if (node?.nodeName === 'A') {
                    node.href = '#';
                } else if (isElement(node)) {
                    node.classList.remove(...allCssClassNames);
                    node.classList.add(cssClassNames[stage]);
                    node.innerHTML = template(context, value);
                } else {
                    node.nodeValue = markup;
                }
            }
        },
        stage
    );

const pendingDomPlaceholderTemplate = createDomHtmlPlaceholderTemplate(
    () => true,
    () => '',
    pending
);

const rejectedDomPlaceholderTemplate = createDomHtmlPlaceholderTemplate(
    () => true,
    () => '...',
    rejected
);

export {
    createDomAttributePlaceholderTemplate,
    createDomHtmlPlaceholderTemplate,
    pendingDomPlaceholderTemplate,
    rejectedDomPlaceholderTemplate,
};
