import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { DialogContainer, Dialog, Content } from '@adobe/react-spectrum';
import { defaultTheme, Provider } from '@adobe/react-spectrum';
import { EnvProvider } from '@pandora/react-env-provider';
import { FetchProvider } from '@pandora/react-fetch-provider';
import { AuthProvider } from '@pandora/react-auth-provider';
import OfferSelectorTool from './OfferSelectorTool';
import { useRef } from 'react';
import AppContext, { createAppContext } from './AppContext';
import { createLog } from '@dexter/tacocat-core';

const log = createLog('OfferSelectorTool');

/**
 * Initiliaze the Offer Selector Tool as a dialog that can be triggered programmatically.
 * @type { import('./types').openAsDialog }
 */
function openAsDialog(rootElement, onSelect, appContext) {
    const closeDialog = () => {
        ReactDOM.unmountComponentAtNode(rootElement);
    };
    const finalAppContext = createAppContext(appContext);
    if (!finalAppContext.onCancel) {
        finalAppContext.onCancel = () => {
            log.debug('Clicked on cancel');
            if (closeDialog) closeDialog();
        };
    }
    function OfferSelectorToolDialog() {
        const [searchArrangementCode, setSearchArrangementCode] = useState(
            finalAppContext.searchArrangementCode
        );
        const [selectOfferId, setSelectOfferId] = useState(
            finalAppContext.selectOfferId
        );
        const resetParams = () => {
            setSearchArrangementCode();
            setSelectOfferId();
        };
        const dialogRef = useRef();
        useEffect(() => {
            const dialogEl = dialogRef.current.UNSAFE_getDOMNode();
            if (!dialogEl) return;
            const modalWrapper = dialogEl.parentElement.parentElement;
            const spectrumRoot = modalWrapper.parentElement;
            spectrumRoot.style.zIndex = 10000;
        }, [dialogRef.current]);

        const authProvider = {
            getAccessToken: () => {
                return finalAppContext.aosConfig.accessToken;
            },
        };
        return (
            <Provider theme={defaultTheme}>
                <EnvProvider value={finalAppContext.environment}>
                    <AuthProvider value={authProvider}>
                        <FetchProvider>
                            <AppContext.Provider
                                value={{
                                    ...finalAppContext,
                                    searchArrangementCode,
                                    selectOfferId,
                                    resetParams,
                                }}
                            >
                                <DialogContainer
                                    onDismiss={closeDialog}
                                    type="modal"
                                >
                                    <Dialog width="1200px" ref={dialogRef}>
                                        <Content>
                                            <OfferSelectorTool
                                                onSelect={onSelect}
                                                onCancel={closeDialog}
                                            />
                                        </Content>
                                    </Dialog>
                                </DialogContainer>
                            </AppContext.Provider>
                        </FetchProvider>
                    </AuthProvider>
                </EnvProvider>
            </Provider>
        );
    }
    ReactDOM.render(<OfferSelectorToolDialog />, rootElement);
    return closeDialog;
}

export { openAsDialog };
