import { createLog } from './log';
import { createGlobal, isObject } from './utilities';

/**
 * @type { import('./types').scaffoldModule }
 */
function scaffoldModule(identity) {
    const log = createLog(identity);
    const getGlobalSet = () =>
        createGlobal(['modules', identity], () => new WeakSet());
    return {
        log,
        createAssert:
            (predicate, message) =>
            (...args) =>
                log.assert(predicate(...args), message, ...args),
        defineModule: (module) => {
            if (module != null) {
                getGlobalSet().add(module);
                Object.defineProperty(module, Symbol.toStringTag, {
                    configurable: true,
                    value: identity,
                });
            }
            return module;
        },
        isModule: (module) => isObject(module) && getGlobalSet().has(module),
    };
}

export { scaffoldModule };
