import {
    createPlaceholderContext,
    createPlaceholderProvider,
    isNotEmptyString,
    omitProperties,
    scaffoldModule,
} from '@dexter/tacocat-core';
import { isWcsClient } from './wcsClient';
import { normaliseCountryAndLanguage } from './wcsUtilities';

const { log, createAssert, defineModule } = scaffoldModule(
    'WcsPlaceholderProvider'
);

const assertClientArgument = createAssert(
    isWcsClient,
    'Argument "client" must be an instance of WcsClient:'
);

/**
 * @type { import('./types').createWcsPlaceholderProvider }
 */
function createWcsPlaceholderProvider(client, ...options) {
    if (!assertClientArgument(client)) return null;

    log.debug('Created:', { client, options });

    /**
     * @param { WcsPlaceholderContext } param0
     */
    function identify(context) {
        const { country, currency, language, offerSelectorId } =
            createPlaceholderContext([context, options], context);
        if (isNotEmptyString(country) && isNotEmptyString(offerSelectorId)) {
            const { env, landscape } = client.options;
            return createPlaceholderContext({
                currency,
                env,
                landscape,
                offerSelectorId,
                ...normaliseCountryAndLanguage(country, language),
            });
        }
        return null;
    }

    /**
     * @param { import('./types').WcsPlaceholderContext[] } contexts
     */
    function provide(contexts) {
        const groups = [];
        for (const context of contexts) {
            const { country, currency, language, offerSelectorId } = context;
            if (
                !isNotEmptyString(country) ||
                !isNotEmptyString(language) ||
                !isNotEmptyString(offerSelectorId)
            ) {
                log.error('Unknown context:', { context });
                continue;
            }
            const languageParameter = country === 'GB' ? 'EN' : 'MULT';
            const locale = `${language.toLowerCase()}_${country.toUpperCase()}`;
            const group = groups.find(
                ({ options }) =>
                    country === options.country &&
                    currency === options.currency &&
                    locale === options.locale &&
                    languageParameter === options.language &&
                    options.offerSelectorIds.length <
                        client.options.offerSelectorLimit
            );
            if (group) {
                group.options.offerSelectorIds.push(offerSelectorId);
                group.contexts.push(context);
            } else {
                groups.push({
                    contexts: [context],
                    options: omitProperties({
                        country,
                        currency,
                        locale,
                        language: languageParameter,
                        offerSelectorIds: [offerSelectorId],
                    }),
                });
            }
        }
        return groups.map(({ contexts, options }) => () => {
            log.debug('Fetching:', { options });
            return client.getWebCommerceArtifact(options).then(
                /**
                 * @param { import('./types').WcsResponse } param0
                 */
                ({ data }) => {
                    log.debug('Fetched:', { data, options });
                    return data.resolvedOffers.flatMap((value) =>
                        value.offerSelectorIds.map((offerSelectorId) => ({
                            context:
                                contexts.find(
                                    (context) =>
                                        context.offerSelectorId ===
                                        offerSelectorId
                                ) ?? createPlaceholderContext(options),
                            value,
                        }))
                    );
                }
            );
        });
    }

    return defineModule(
        createPlaceholderProvider(identify, provide, ...options)
    );
}

export { createWcsPlaceholderProvider };
