import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import { defaultTheme, Provider } from '@adobe/react-spectrum';
import { EnvProvider } from '@pandora/react-env-provider';
import { FetchProvider } from '@pandora/react-fetch-provider';
import { AuthProvider } from '@pandora/react-auth-provider';
import { defaultPriceLiterals } from '@dexter/tacocat-consonant-templates';
import OfferSelectorTool from '../src/OfferSelectorTool';
import AppContext, { createAppContext } from '../src/AppContext';
import { openAsDialog } from '../src/dialog';

import {
    pendingDomPlaceholderTemplate,
    rejectedDomPlaceholderTemplate,
} from '@dexter/tacocat-dom-placeholders';
import {
    createWcsClient,
    createWcsPlaceholderProvider,
    wcsPriceDomPlaceholderTemplate,
    wcsPriceOpticalDomPlaceholderTemplate,
    wcsPriceStrikethroughDomPlaceholderTemplate,
} from '@dexter/tacocat-wcs-client';
import { createPlaceholderEngine } from '@dexter/tacocat-core';

import styles from './index.css';
import {
    Environment,
    Landscape,
    ProviderEnvironment,
} from '@pandora/data-source-utils';

import { createLog } from '@dexter/tacocat-core';

const log = createLog('Offer Selector Tool');

if (styles) {
    // swc hack.
}

let closeDialog;

let onCancelCustom = () => {
    log.debug('Clicked on cancel - custom behaviour');
    if (closeDialog) closeDialog();
};

const engine = (wcsApiKey, wcsBaseUrl) =>
    createPlaceholderEngine().register(
        createWcsPlaceholderProvider(
            createWcsClient({
                apiKey: wcsApiKey,
                env,
                environment,
                landscape,
                baseUrl: wcsBaseUrl,
            }),
            {
                checkoutWorkflow: 'UCv3',
            }
        ),
        wcsPriceDomPlaceholderTemplate,
        wcsPriceOpticalDomPlaceholderTemplate,
        wcsPriceStrikethroughDomPlaceholderTemplate,
        pendingDomPlaceholderTemplate,
        rejectedDomPlaceholderTemplate
    );

const placeholderTypes = [
    {
        type: 'price',
        name: 'Price',
        description: 'Formatted price, can be inlined with neighbour text',
        doc: '#',
    },
    {
        type: 'priceOptical',
        name: 'Optical price',
        description:
            'Formatted price calculating monthly payments for annual plans, can be inlined with neighbour text',
        doc: '#',
    },
    {
        type: 'priceStrikethrough',
        name: 'Strikethrough price',
        description:
            'Formatted price displayed as strikethrough, can be inlined with neighbour text',
        doc: '#',
    },
];

const country = 'US';
const language = 'en';
const env = ProviderEnvironment.STAGE;
// see https://developers.corp.adobe.com/aos/docs/guide/overview.md#DataPlanes
const environment = Environment.PRODUCTION;
let landscape = Landscape.DRAFT;
let wcsBaseUrl = 'https://wcs-stage.adobe.io';
let wcsApiKey = 'dexter-commerce-web-artifacts';
let aosApiKey = 'dexter-commerce-offers';

if (env === ProviderEnvironment.PRODUCTION) {
    wcsBaseUrl = 'https://wcs.adobe.io';
    landscape = Landscape.PUBLISHED;
    wcsApiKey = 'wcms-commerce-ims-ro-user-cc';
    aosApiKey = 'wcms-commerce-ims-user-prod';
}

/**
  @type { import('../src/types').ContextParams }
 */
const appContext = {
    aosParams: {
        marketSegment: 'COM',
        offerType: 'BASE',
    },
    apiKey: aosApiKey,
    accessToken:
        process.env.AOS_ACCESS_TOKEN ||
        localStorage.getItem('AOS_ACCESS_TOKEN'),
    env,
    environment,
    landscape,
    onCancel: onCancelCustom,
    engine: engine(wcsApiKey, wcsBaseUrl),
    country,
    globals: [
        {
            literals: defaultPriceLiterals,
            checkoutWorkflow: 'UCv3',
            country,
            language,
        },
    ],
    defaultPlaceholderOptions: {
        displayFormatted: true,
        displayRecurrence: true,
        displaySeat: false,
        displayTax: false,
    },
    offerSelectorPlaceholderOptions: {
        // displayFormatted: false,
    },
    placeholderTypes,
    showPlaceholderOptions: true,
    // showPlaceholders: false,
    // disableOfferSelection: true,
    searchArrangementCode: 'ccsn_direct_individual',
    selectOfferId: '9E618D5A589EF8D6364DFBE02FC2C264',
};

const root = document.getElementById('root');
const params = new URLSearchParams(document.location.search);
const test = params.get('test');
switch (test) {
    case 'dialog':
        {
            const onSelectHandler = (offerSelectorId, type, offer, options) => {
                log.debug(offerSelectorId, type, offer, options);
                closeDialog();
            };
            closeDialog = openAsDialog(root, onSelectHandler, appContext);
        }
        break;
    default:
        {
            const finalAppContext = createAppContext(appContext);
            const authProvider = {
                getAccessToken: () => {
                    return finalAppContext.aosConfig.accessToken;
                },
            };
            const onSelectHandler = (offerSelectorId, type, offer, options) => {
                log.debug(offerSelectorId, type, offer, options);
            };
            const App = () => {
                const [searchArrangementCode, setSearchArrangementCode] =
                    useState(finalAppContext.searchArrangementCode);
                const [selectOfferId, setSelectOfferId] = useState(
                    finalAppContext.selectOfferId
                );
                const resetParams = () => {
                    setSearchArrangementCode();
                    setSelectOfferId();
                };
                return (
                    <Provider theme={defaultTheme}>
                        <EnvProvider value={finalAppContext.environment}>
                            <AuthProvider value={authProvider}>
                                <FetchProvider>
                                    <AppContext.Provider
                                        value={{
                                            ...finalAppContext,
                                            searchArrangementCode,
                                            selectOfferId,
                                            resetParams,
                                        }}
                                    >
                                        <OfferSelectorTool
                                            onSelect={onSelectHandler}
                                        />
                                    </AppContext.Provider>
                                </FetchProvider>
                            </AuthProvider>
                        </EnvProvider>
                    </Provider>
                );
            };

            ReactDOM.render(<App />, root);
        }
        break;
}

if (module.hot) {
    module.hot.accept();
}
