import {
    createPlaceholderContext,
    isPlaceholderContext,
} from './placeholderContext';
import { isPlaceholderProvider } from './placeholderProvider';
import {
    createPlaceholderResolver,
    isPlaceholderResolver,
} from './placeholderResolver';
import { scaffoldModule } from './scaffolds';
import { createPlaceholderState } from './placeholderState';
import { placeholderStages } from './placeholderStages';
import { isPlaceholderTemplate } from './placeholderTemplate';

/**
 * @typedef { import('./types').PlaceholderContext<unknown> } PlaceholderContext
 * @typedef { import('./types').PlaceholderProvider<unknown, unknown> } PlaceholderProvider
 * @typedef { import('./types').placeholderStage } placeholderStage
 * @typedef { import('./types').PlaceholderState } PlaceholderState
 * @typedef { import('./types').PlaceholderTemplate<unknown, unknown> } PlaceholderTemplate
 */

const { pending, rejected, resolved } = placeholderStages;

const { log, createAssert, defineModule, isModule } =
    scaffoldModule('PlaceholderEngine');

const assertContextArgument = createAssert(
    isPlaceholderContext,
    'Argument "context" must be an instance of PlaceholderContext:'
);
const assertModuleArgument = createAssert(
    (value) => isPlaceholderProvider(value) || isPlaceholderTemplate(value),
    'Argument "module" must be an instance of PlaceholderProvider or PlaceholderTemplate type:'
);
const assertResolverArgument = createAssert(
    isPlaceholderResolver,
    'Argument "resolver" must be an instance of PlaceholderResolver:'
);

/**
 * @type { import('./types').createPlaceholderEngine }
 */
function createPlaceholderEngine(resolver) {
    let engine;

    if (resolver == null) resolver = createPlaceholderResolver();
    else if (!assertResolverArgument(resolver)) {
        return null;
    }

    /**
     * @type { Set<PlaceholderProvider> }
     */
    const providers = new Set();
    /**
     * @type { Map<PlaceholderProvider, Map<PlaceholderContext, PlaceholderState>> }
     */
    const providerQueues = new Map();
    /**
     * @type { Map<string, PlaceholderState> }
     */
    const providerStates = new Map();
    /**
     * @type { { [stage in placeholderStage]: Set<PlaceholderTemplate> } }
     */
    const templates = {
        [pending]: new Set(),
        [resolved]: new Set(),
        [rejected]: new Set(),
    };
    /**
     * @type { Map<string, PlaceholderState> }
     */
    const templateStates = new Map();
    let timer;

    log.debug('Created:', { resolver });

    function dispose() {
        if (timer) clearTimeout(timer);
        timer = 0;
        providers.clear();
        providerQueues.clear();
        providerStates.clear();
        templates[pending].clear();
        templates[resolved].clear();
        templates[rejected].clear();
        templateStates.clear();
        log.debug('Disposed');
    }

    /**
     * @param { PlaceholderState } templateState
     * @param { placeholderStage } stage
     * @param { any } value
     */
    function connect(providerState, templateState) {
        providerState.subscribe(function format(stage, value) {
            const context = createPlaceholderContext({
                ...templateState.context,
                ...providerState.context,
            });
            const template = [...templates[stage]].find((candidate) =>
                candidate.accept(context, value)
            );
            if (template) {
                try {
                    templateState.update(
                        stage,
                        template.format(context, value)
                    );
                } catch {
                    // ignore
                }
            } else if (stage === resolved) {
                log.error('Template not found:', { context });
            }
        });
    }

    /**
     * @param  { ...(PlaceholderProvider | PlaceholderTemplate) } modules
     */
    function register(...modules) {
        for (const module of modules.flat(2)) {
            if (!assertModuleArgument(module)) return;
            log.debug('Registering:', { module });
            if (isPlaceholderTemplate(module)) {
                templates[module.stage].add(module);
            } else if (isPlaceholderProvider(module)) {
                providers.add(module);
            }
        }
        return engine;
    }

    /**
     * @param { PlaceholderContext } templateContext
     */
    function resolve(templateContext) {
        if (!assertContextArgument(templateContext)) return null;
        const templateContextKey = JSON.stringify(templateContext);
        let templateState = templateStates.get(templateContextKey);
        if (!templateState) {
            log.debug('Resolving:', { context: templateContext });
            let provider, providerContext;
            for (provider of providers) {
                try {
                    providerContext = provider.identify(templateContext);
                    if (providerContext != null) break;
                } catch (error) {
                    log.error('Provider error:', error);
                }
            }
            if (providerContext == null) {
                log.error('Provider not found:', { context: templateContext });
                return {
                    subscribe(observer) {
                        observer(rejected, undefined);
                        return () => {};
                    },
                    unsubscribe() {},
                };
            }
            const providerContextKey = JSON.stringify(providerContext);
            let providerState = providerStates.get(providerContextKey);
            if (!providerState) {
                providerState = createPlaceholderState(providerContext);
                providerStates.set(providerContextKey, providerState);
                schedule(provider, providerContext, providerState);
            }
            templateState = createPlaceholderState(templateContext);
            templateStates.set(templateContextKey, templateState);
            connect(providerState, templateState);
        }
        const { subscribe, unsubscribe } = templateState;
        return { subscribe, unsubscribe };
    }

    function schedule(provider, providerContext, providerState) {
        let queue = providerQueues.get(provider);
        if (!queue) {
            queue = new Map();
            providerQueues.set(provider, queue);
        }
        queue.set(providerContext, providerState);

        if (timer) return;
        log.debug('Scheduled');
        timer = setTimeout(async () => {
            timer = 0;
            for (const [provider, scheduled] of providerQueues) {
                if (!scheduled.size) continue;
                const resolving = new Map(scheduled);
                scheduled.clear();
                const contexts = [...resolving.keys()];
                log.debug('Providing:', { contexts, provider });
                const fetchers = provider.provide([...contexts]);
                const promises = fetchers.map(async (fetcher) => {
                    try {
                        const entries = await resolver.resolve(fetcher);
                        if (!providers.has(provider)) return;
                        for (const { context, value } of entries) {
                            log.debug('Provided:', { context, value });
                            let providerState = resolving.get(context);
                            if (providerState) {
                                resolving.delete(context);
                            } else {
                                const key = JSON.stringify(context);
                                providerState = providerStates.get(key);
                                if (!providerState) {
                                    providerState =
                                        createPlaceholderState(context);
                                    providerStates.set(key, providerState);
                                }
                            }
                            providerState.update(resolved, value);
                        }
                    } catch (error) {
                        log.error('Provider error:', { contexts }, error);
                    }
                });
                await Promise.all(promises);
                if (resolving.size) {
                    log.error('Not resolved:', {
                        contexts: [...resolving.keys()],
                    });
                    for (const state of resolving.values()) {
                        state.update(rejected, undefined);
                    }
                }
            }
        });
    }

    engine = defineModule({
        dispose,
        register,
        resolve,
    });
    return engine;
}

/**
 * @type { import('./types').isPlaceholderEngine }
 */
const isPlaceholderEngine = isModule;

export { createPlaceholderEngine, isPlaceholderEngine };
