import React from 'react';
import {
    Environment,
    Landscape,
    ProviderEnvironment,
} from '@pandora/data-source-utils';
import defaultProducts from '../data/products.json';

/**
  @type { import('./types').AosConfig }
 */
const defaultAosConfig = {
    apiKey: '',
    accessToken: '',
    landscape: Landscape.DRAFT,
};

/**
 @type { import('./types').AosParams }
 */
const defaultAosParams = {
    commitment: '',
    term: '',
    customerSegment: 'INDIVIDUAL',
    offerType: 'BASE',
    marketSegment: 'COM',
};

/**
 @type { import('./types').ContextParams }
 */
const defaultAppValues = {
    products: defaultProducts,
    checkoutWorkflow: 'UCv3',
    searchArrangementCode: '',
    baseUrl: String,
    env: ProviderEnvironment.STAGE,
    environment: Environment.STAGE,
    landscape: Landscape.PUBLISHED,
    defaultPlaceholderOptions: {
        displayFormatted: true,
        displayRecurrence: true,
        displaySeat: false,
        displayTax: false,
    },
    showPlaceholders: true,
    disableOfferSelection: false,
    aosParams: defaultAosParams,
    aosConfig: defaultAosConfig,
    country: 'US',
    selectOfferId: undefined,
    resetParams: () => {},
};

function createAppContext({
    accessToken,
    aosParams = {},
    apiKey,
    products = defaultProducts,
    checkoutWorkflow = defaultAppValues.checkoutWorkflow,
    searchArrangementCode,
    onCancel,
    baseUrl,
    env,
    environment,
    landscape,
    defaultPlaceholderOptions = defaultAppValues.defaultPlaceholderOptions,
    offerSelectorPlaceholderOptions = defaultAppValues.defaultPlaceholderOptions,
    showPlaceholders = true,
    showPlaceholderOptions = false,
    disableOfferSelection = false,
    country = defaultAppValues.country,
    selectOfferId,
    engine,
    globals = [],
    placeholderTypes,
}) {
    return {
        ...defaultAppValues,
        accessToken,
        aosParams: Object.assign({ ...defaultAosParams }, aosParams),
        apiKey,
        products,
        checkoutWorkflow,
        searchArrangementCode,
        onCancel,
        baseUrl,
        env,
        environment,
        landscape,
        defaultPlaceholderOptions: {
            ...defaultAppValues.defaultPlaceholderOptions,
            ...defaultPlaceholderOptions,
        },
        offerSelectorPlaceholderOptions: {
            ...defaultAppValues.defaultPlaceholderOptions,
            ...defaultPlaceholderOptions,
            ...offerSelectorPlaceholderOptions,
        },
        showPlaceholders: disableOfferSelection ? false : showPlaceholders,
        showPlaceholderOptions,
        disableOfferSelection,
        country,
        selectOfferId: disableOfferSelection ? undefined : selectOfferId,
        engine,
        globals,
        placeholderTypes,
    };
}

const AppContext = React.createContext(defaultAppValues);

export default AppContext;
export { createAppContext };
